<template>
    <div class="app">
        <Sidebar ref="sidebar"></Sidebar>
        <main class="app-body">
            <Nav></Nav>
            <div class="app-body__inner">
                <Header :title="title"></Header>
                <div v-if="alerts.length > 0" class="app-alert">
                    <Alert v-for="(alert, index) in alerts" :key="`${alert.timestamp}-${index}`" v-bind="alert"></Alert>
                </div>
                <slot></slot>
            </div>
        </main>
        <form id="logout-form" action="/logout" method="POST" style="display: none;">
            <input type="hidden" name="_token" :value="token">
        </form>
    </div>
</template>

<script>
    import Alert from './../Alert';
    import Header from './Header';
    import Nav from './Nav';
    import Sidebar from './Sidebar';

    export default {
        components: {
            Alert,
            Header,
            Nav,
            Sidebar,
        },

        mounted() {
            const title = document.title;

            document.title = `${title} | ${this.title}`;

            this.$inertia.on('finish', () => {
                document.title = `${title} | ${this.title}`;
            });
        },

        computed: {
            token() {
                return this.$page.props.csrf_token;
            },
            alerts() {
                return this.$page.props.alerts;
            },
            title() {
                return this.$page.props.title || this.__('Dashboard');
            },
        },
    }
</script>
